import React from "react";

const ReferralItem = ({img, title, desc})=>{
    return (
        <div className="d-flex">
            <div className="img">
                <img src={img}/>
            </div>
            <div className="content">
                <h4>{title}</h4>
                <p>
                    {desc}
                </p>
            </div>
        </div>
    )
}

export default ReferralItem;