import React from "react";
import './introduce.scss';
import logo from "../../assets/img/IOX_LOGO.png";
import { Link } from "react-router-dom";
import { TOKEN_NAME } from "../../contracts/contractInfo";

const Introduce = ()=>{
    return (
        <div className="introduce" id="first">
            <div className="container p-15">
                <div className="d-flex">
                    <div>
                        <h2 className="title">What is IOX</h2>
                        <p className="desc">
                            At is core IOX is a revolutionary staking DApp designed to give users great return on investment. Drawing inspiration from the Avarice platform, IOX present a more durable and user-friendly method of staking token; IOX brings the auction fun and FOMO to the Binance Smart Chain.
                        </p>
                        <div className="d-flex mt-20">
                            <a href="/lobby" className="btn btn--orange">Get ${TOKEN_NAME} Token</a>
                            <a href="/ioxproject.pdf" className="btn btn--blue">Litepaper</a>
                        </div>
                    </div>
                    <div className="logo">
                        <img src={logo}></img>
                    </div>
                </div>
                
            </div>
        </div>
    )    
}

export default Introduce;