import React, { useState } from "react";
import { CONTACT_MAIL } from "../../contracts/contractInfo";

const Contact = ()=>{
    const [formData, setFormData] = useState({
        title: "",
        email: "",
        message: ""
    });

    const HandleInput = (ev)=>{
        let name = ev.target.name;
        let val = ev.target.value;
        setFormData({
            ...formData,
            [name]: val
        })
    }

    const sendMail = () => {
        console.log(formData);
        if(formData.email && formData.title) {
            var link = "mailto:" + CONTACT_MAIL
            + `?cc=${formData.email}`
            + `&subject=${formData.title}`
            + `&body=${formData.message}`;
   
            window.location.href = link;
        }
    }

    return (
        <div className="container">
            <div className="contact-page">
                <small>CONTACT US</small>
                <h3>Get in Touch</h3>
                <form className="form">
                    <input className="input-box" name="title" value={formData.title} onChange={(ev)=>HandleInput(ev)} placeholder="Title" required/>
                    <input className="input-box" name="email" value={formData.email} onChange={(ev)=>HandleInput(ev)} placeholder="Email" required/>
                    <textarea rows="10" className="input-box" value={formData.message} name="message" onChange={(ev)=>HandleInput(ev)} placeholder="Message" required>{formData.message}</textarea>
                    <button className="btn btn--orange submit" onClick={()=>sendMail()}>SEND MESSAGE</button>
                </form>
            </div>
        </div>
    )
}

export default Contact;