import React, { useState } from "react";
import ReferralImg from '../../assets/img/referral.png';
import SecureImg from '../../assets/img/secure.png';
import SocialImg from '../../assets/img/social_media.png';
import MobileOptImg from '../../assets/img/mobile_opt.png';
import ReferralItem from "./ReferralItem";

const Referral = ()=>{
    const [items, setItems] = useState([
        {
            img: ReferralImg,
            title: "Referral System",
            desc: "Avarice has a grate referral system that rewards both referer and the referred user. everyone wins when using a ref link! There's zero reason NOT to use one!"
        },
        {
            img: SecureImg,
            title: "Secure",
            desc: "Avarice smart contract is verified and audited. Rest assured you can trust your funds are safe."
        },
        {
            img: SocialImg,
            title: "Social Media",
            desc: "Get all the latest updates and connect with other active members of the community using Telegram and Discord"
        },
        {
            img: MobileOptImg,
            title: "Mobile Optimized",
            desc: "On the go? The Avarice platform is optimized for mobile use. supporting all of the platform features on your smart device."
        }
    ]);
    return (
        <div id="feature">
            <div className="container">
                <div className="row">
                {
                    items.map((item, key)=>
                        (
                            <div className="referral-item" key={key}>
                                <ReferralItem {...item}></ReferralItem>
                            </div>
                        )
                    )
                }
                </div>
            </div>
        </div>
    )
}

export default Referral;